.footer{
  width: 880px;
  height: max-content;
  margin: 67px 0 60px 0;
}

@media screen and (min-width: 650px) and (max-width: 950px) {
  .footer {
    width: 600px;
  }
}

@media screen and (min-width: 320px) and (max-width: 650px) {
  .footer {
    width: 282px;
    margin: 55px 0 60px 0;
  }
}